import React, { useState } from "react";
import "./MenuAdmin.css";
import VistaNegocio from "./VistaNegocio"; // Importamos la interfaz VistaNegocio
import VistaUsuario from "./VistaUsuario"; // Puedes crear este archivo o usar otro componente como ejemplo
import VistaReseña from "./VistaReseña"; // Puedes crear este archivo o usar otro componente como ejemplo

const MenuAdmin = () => {
  const [activeComponent, setActiveComponent] = useState(""); // Controla qué componente se muestra

  const loadComponent = (component) => {
    setActiveComponent(component);
  };

  const renderComponent = () => {
    switch (activeComponent) {
      case "vistausuario":
        return <VistaUsuario />; // Carga la interfaz de validación de usuarios
      case "vistanegocio":
        return <VistaNegocio />; // Carga la interfaz de monitoreo de negocios
      case "vistareseña":
        return <VistaReseña />; // Carga la interfaz de monitoreo de reseñas
      default:
        return (
          <div className="dashboard">
            <h2>¡Bienvenido al Panel de Administración!</h2>
            <p>Selecciona una opción del menú para empezar.</p>
          </div>
        );
    }
  };

  return (
    <div className="admin-container">
      {/* Barra lateral */}
      <nav className="admin-menu">
        <h3 className="menu-title">Panel de Administración</h3>
        <ul className="menu-list">
          <li
            className={activeComponent === "vistausuario" ? "active" : ""}
            onClick={() => loadComponent("vistausuario")}
          >
            Validar Usuarios
          </li>
          <li
            className={activeComponent === "vistanegocio" ? "active" : ""}
            onClick={() => loadComponent("vistanegocio")}
          >
            Monitorear Negocios
          </li>
          <li
            className={activeComponent === "vistareseña" ? "active" : ""}
            onClick={() => loadComponent("vistareseña")}
          >
            Monitorear Reseñas
          </li>
        </ul>
      </nav>

      {/* Contenido */}
      <div className="admin-main">
        <header className="admin-header">
          <span>Bienvenido, Administrador</span>
          <button className="btn-logout">Cerrar Sesión</button>
        </header>
        <div className="content">{renderComponent()}</div>
      </div>
    </div>
  );
};

export default MenuAdmin;
